<template>
  <div id="app">
    <!-- <div class="music-play-box" :class="{'musicBoxShow':musicBoxShow}">
      <a class="switch" href="javascript:void(0)" @click="musicBoxShow=!musicBoxShow" v-if="playlist.length > 0">
        <jam-arrow-square-right v-if="musicBoxShow"/>
        <jam-arrow-square-left v-if="!musicBoxShow"/>
      </a>
      
    </div> -->
    <music-player></music-player>
    <div class="container">
      <global-header></global-header>
      <tab></tab>
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapGetters } from "vuex";
import GlobalHeader from 'components/global/global-header'
import Tab from 'components/tab/tab'
import MusicPlayer from 'components/music/music-play'

export default {
  components: {
    GlobalHeader,
    Tab,
    MusicPlayer
  },
  computed: {
      ...mapGetters(['playlist'])
  },
  data () {
    return {
      musicBoxShow: false,
      currentMp3: '',
    }
  },
  created () {
    console.log('\r\n130594899 \r\n150874160 \r\n662933178 \r\n284331448 \r\n221771881 \r\n208081470 \r\n460370278 \r\n511716523 \r\n290102632 \r\n277655492 \r\n306979367 \r\n390766661 \r\n236647306 \r\n180764582 \r\n275742112 \r\n219365999 \r\n121545472 \r\n383411206 \r\n200264670 \r\n316835439 \r\n327766791 \r\n132204035 \r\n364510713 \r\n471109965 \r\n579439188 \r\n101724227 \r\n609941641 SNH48-Team SII ')
    console.log('\r\nSNH48 Team SII ')
  },
  name: 'App'
}
</script>

<style lang="stylus" rel="stylesheet/stylus" >
@import '~common/stylus/index'

@media screen and (min-width 1366px)
  #app
    height 100%
    background-image url('/TeamSII/dist/static/images/bg.jpg')
    background-repeat no-repeat
    background-position top center
    background-size contain
    color #2c3e50
    padding 15px 0
    background-color $color-bg
    .container
      width 1336px
      margin 0 auto
    .music-play-box
      position fixed
      right -400px
      top 290px
      height calc(100% - 290px)
      width 400px
      z-index 5
      transition all .6s ease-in-out
      background $color-team-sii
      &.musicBoxShow
        right 0
      a.switch
        padding 0.5em
        width auto
        height auto
        position absolute
        right 100%
        top calc(50% - 1em)

@media screen and (max-width 1366px)
  #app
    height 100%
    background-image url('/TeamSII/dist/static/images/bg.jpg')
    background-repeat no-repeat
    background-position top center
    background-size contain
    color #2c3e50
    padding 15px 0
    background-color $color-bg
    .container
      width 100%
      margin 0 auto
      padding 0 10px
</style>



